/* eslint-disable import/no-extraneous-dependencies */
import { LitElement, TemplateResult, css, html } from 'lit';
import { property } from 'lit/decorators.js';

import '@material/mwc-button';
import '@material/mwc-icon';
import '@material/mwc-list';

import { validateSchema } from './schema/validateSchema.js';
import { validateTemplates } from './schema/validateTemplates.js';
import { IssueDetail } from './foundation/utils.js';

/** An editor [[`plugin`]] to configure validators and display their issue centrally */
export default class SclValidatingPlugin extends LitElement {
  /** The document being edited as provided to plugins by [[`OpenSCD`]]. */
  @property({ attribute: false })
  doc!: XMLDocument;

  /** The name of the document being edited */
  @property()
  docName!: string;

  /** SCL change indicator */
  @property({ type: Number })
  editCount = 0;

  @property()
  schemaIssues: IssueDetail[] = [];

  @property()
  templateIssues: IssueDetail[] = [];

  private async validateSchema(): Promise<void> {
    this.schemaIssues.length = 0;
    this.schemaIssues = await validateSchema(this.doc, this.docName);
    this.requestUpdate('schemaIssues');
  }

  private async validateTemplates(): Promise<void> {
    this.templateIssues.length = 0;
    for await (const issue of validateTemplates(this.doc)) {
      this.templateIssues.push(...issue);
      this.requestUpdate('templateIssues');
    }
  }

  async performUpdate() {
    // eslint-disable-next-line no-promise-executor-return
    await new Promise<void>(resolve => requestAnimationFrame(() => resolve()));
    super.performUpdate();
  }

  // eslint-disable-next-line class-methods-use-this
  private renderIssueEntry(issue: IssueDetail): TemplateResult {
    return html` <abbr title="${`${issue.title}\n${issue.message}`}"
      ><mwc-list-item ?twoline=${!!issue.message}>
        <span> ${issue.title}</span>
        <span slot="secondary">${issue.message}</span>
      </mwc-list-item></abbr
    >`;
  }

  private renderValidatorsIssues(issues: IssueDetail[]): TemplateResult[] {
    if (issues.length === 0) return [html``];
    return [
      html`<li divider padded role="separator"></li>`,
      ...issues.map(issue => this.renderIssueEntry(issue)),
    ];
  }

  render(): TemplateResult {
    return html`<section>
      <div>
        <mwc-button
          label="validate schema"
          @click="${this.validateSchema}"
        ></mwc-button
        ><mwc-list id="content" wrapFocus
          >${this.renderValidatorsIssues(this.schemaIssues)}</mwc-list
        >
      </div>
      <div>
        <mwc-button
          label="validate templates"
          @click="${this.validateTemplates}"
        ></mwc-button
        ><mwc-list id="content" wrapFocus
          >${this.renderValidatorsIssues(this.templateIssues)}</mwc-list
        >
      </div>
    </section>`;
  }

  static styles = css`
    section {
      display: grid;
      gap: 12px;
      padding: 8px 12px 16px;
      box-sizing: border-box;
      grid-template-columns: repeat(auto-fit, minmax(316px, auto));
    }
  `;
}
