import {
  IssueDetail,
  ValidationResult,
  Validator,
  WorkerMessage,
  isLoadSchemaResult,
  isValidationError,
  isValidationResult,
} from '../foundation/utils.js';
import { getSchema } from '../foundation/schemas.js';

const validators: Partial<Record<string, Validator>> = {};

export async function validateSchema(
  doc: XMLDocument,
  docName: string
): Promise<IssueDetail[]> {
  const issues: IssueDetail[] = [];

  const fileName = docName;
  let version = '2007';
  let revision = 'B';
  let release = '1';

  if (doc.documentElement)
    [version, revision, release] = [
      doc.documentElement.getAttribute('version') ?? '',
      doc.documentElement.getAttribute('revision') ?? '',
      doc.documentElement.getAttribute('release') ?? '',
    ];

  async function getValidator(
    xsd: string,
    xsdName: string
  ): Promise<Validator> {
    if (!window.Worker) throw new Error('Invalid schema');
    if (validators[xsdName]) return validators[xsdName]!;

    const worker: Worker = new Worker('/public/worker.js');

    async function validate(
      xml: string,
      xmlName: string
    ): Promise<ValidationResult> {
      return new Promise(resolve => {
        worker.addEventListener('message', (e: MessageEvent<WorkerMessage>) => {
          if (isValidationResult(e.data) && e.data.file === xmlName)
            resolve(e.data);
          else if (isValidationError(e.data)) {
            const parts = e.data.message.split(': ', 2);
            const description = parts[1] ? parts[1] : parts[0];
            const qualifiedTag = parts[1] ? ` (${parts[0]})` : '';
            issues.push({
              title: description,
              message: `${e.data.file}:${e.data.line} ${e.data.node} ${e.data.part}${qualifiedTag}`,
            });
          }
        });
        worker.postMessage({ content: xml, name: xmlName });
      });
    }

    validators[xsdName] = validate;

    return new Promise((resolve, reject) => {
      worker.addEventListener('message', (e: MessageEvent<WorkerMessage>) => {
        if (isLoadSchemaResult(e.data)) {
          if (e.data.loaded) resolve(validate);
          // eslint-disable-next-line prefer-promise-reject-errors
          else reject('validator.schema.loadEror');
        }
      });
      worker.postMessage({ content: xsd, name: xsdName });
    });
  }

  const validate = await getValidator(
    getSchema(version, revision, release),
    `SCL${version}${revision}${release}.xsd`
  );

  const result = await validate(
    new XMLSerializer().serializeToString(doc),
    fileName
  );

  if (result.valid) issues.push({ title: 'Project is schema valid' });

  return issues;
}
